import modal from '../../../element/modal.vue'
import HMmessages from "@/components/HM-messages/HM-messages.vue"
let timer = require('../../../commen/util.js')
let time = timer.formatTime(new Date())
let db = wx.cloud.database()
let user = db.collection('user')
let dbComment = db.collection('comments')
export default{
	name:'comment',
	props:{
		toCommentTab:Array,
		toCommentDetails:Array,
		data:String,
	},
	components:{
		modal,
		HMmessages
	},
	data(){
		return{
			// defaultKind:'全部',
			idData:'',
			theCommentTab:[],
			time:time,
			avatarUrl:'',
			nickName:'',
			comment:'',
			uploadComment:'',
			showMask:false,
			num:0,
		}
	},
	methods:{
		handleTab(index,item){
			this.num = index
			console.log(item)
			this.$parent.renderComment(item)
		},
		handleInput(){
			user.get()
			.then(res=>{
				if(res.data.length === 0){
					this.$refs.mon.show = true
				}else{
					let theUser = res.data[0]
					this.avatarUrl = theUser.avatarUrl
					this.nickName = theUser.nickName
					this.showMask = true
				}
			})
			.catch(err=>{
				console.log(err)
			})
		},
		cancleComment(){
			this.showMask = false
			this.comment = ''
		},
		sureComment(){
			if(this.comment === ''){
				let info = '评论不能为空'
				let status = 'error'
				this.remind(info,status)
			}else{
				this.submit()
			}
		},
		async submit(){
			let theComment = await this.fnComment()
			let commentTab = this.handleComment(theComment)
			let avatarUrl = this.avatarUrl
			let nickName = this.nickName
			let id = this.idData
			let currentTime = time
			dbComment.add({
				data:{
					commentTab,
					id,
					aboutComment:{
						avatarUrl,
						nickName,
						time:currentTime,
						comment:this.comment
					}
				}
			}).then(res=>{
				this.comment = ''
				let info = '评论成功'
				let status = 'success'
				this.remind(info,status)
				let item = '全部'
				this.$parent.renderComment(item)
				console.log('上传成功')
			}).catch(err=>{
				console.log('出错了')
			})
			console.log(commentTab)
		},
		handleComment(comment){
			if( comment.result.nsMessage.items.length === 0){
				return ''
			}
			let items = comment.result.nsMessage.items
			let theItem = items[items.length - 1]
			let [prop,adj] = [theItem['prop'],theItem['adj']]
			return prop + adj
		},
		fnComment(){
			return new Promise((resolve,reject)=>{
				wx.cloud.callFunction({
					name:'yyhBaidu',
					data:{
						message:this.comment
					}
				})
				.then(res=>{
					resolve(res)
				})
				.catch(err=>{
					reject(err)
				})
			})
		},
		remind(info,status){
			this.HMmessages.show(info,{icon:status,background:'coral',fontColor:'#fff'})
		}
	},
	watch:{
		toCommentTab(newVal,oldVal){
			this.theCommentTab = ['全部',...newVal]
			this.num = 0
		},
		data(newVal,oldVal){
			this.idData = newVal
			console.log(this.idData)
		}
	}
}
